							<img src="../../static/images/yz1.jpg" alt="">
						</span>
						<a href="javascript:void(0)" class="change-code">
							换一张
						</a>
					</li>
					<li class="phone">
						<input type="number" placeholder="请输入手机号" style="outline:none;">
						<b class="error"></b>
					</li>
					<li class="phone-yzm">
						<input  class="phone-yzm-inp" type="number" placeholder="请输入手机验证码" style="outline:none;">
