						<b class="error"></b>
					</li>
					<li class="password2">
						<input type="password" placeholder="请再一次输入密码" style="outline:none;">
						<b class="error"></b>
					</li>
					<li class="check">
						<input type="checkbox" class="check-agree">
						<label for="_isAgreePhone">我已阅读并接受</label>
						<a target="_blank" href="/warranty/wapprotocol.html" class="check-item text-green">《vivo服务协议》<label for="_isAgreePhone">我已阅读并接受</label></a>
					</li>
				</ul>
