</template>
<script>
	export default{
		data () {
			return {
				isShow:false
			}
		},
		methods:{
			showabc () {
				this.isShow = true;
			}
		}
	}
</script>
<style scoped lang="scss">
@function rem($px) {
    @return $px/108px * 1rem;
}
.fade-enter{
	transform:translateX(100%);
}
.fade-enter-to{
	transform:translateX(0);
	transition: all .7s;
}
.fade-leave{
	transform:translateX(0);
}
.fade-leave-active{
	transform:translateX(-100%);
	transition: all .7s;
}
*{
	padding: 0;
	margin: 0 auto;
}
.container{
	font-family: "Arial";
    font-size: 75%;
    color: #000;
    background-color: #008cd6;
    margin: 0;
    padding: 0;
    font-weight: normal;
    a {
	    color: #999999;
	    text-decoration: none;
	    outline: none;
	    cursor: pointer;
	}
}
.header{
	width: 100%;
	overflow: hidden;
}
.header-cont{
	width: 100%;
	margin-bottom: rem(36px);
	p{
		font-size: rem(60px);
	    color: #fff;
	    font-weight: normal;
	    margin: 0 auto rem(75px);
	    text-align: center;
	}
}
.logo{
	display: block;
	width: rem(390px);
	height: rem(105px);
	margin:rem(90px) auto rem(75px);
	img{
		width: rem(390px);
		height: rem(105px);
	}
}
.login-list{
	border-radius: rem(15px);
    background-color: #fff;
    width: 95%;
    margin: rem(36px) auto rem(108px);
    li{
    	border-bottom: 1px solid #A3CBEB;
	    text-align: center;
	    line-height: rem(135px);
	    position: relative;
	    padding: 0 rem(36px);
	    text-align: left;
	    b {
	    	display: none;
		    width: rem(69px);
		    height: rem(69px);
		    overflow: hidden;
		    background: url('../../static/images/m-regjudge-ico.png') no-repeat;
		    background-size:rem(138px) rem(69px);
		    position: absolute;
		    top: rem(30px);
		    right: rem(24px);
		}
		.current{
			background-position: rem(-69px) 0;
			display: block;
		}
    }
    .name-inp,.phone-yzm{
    	b{
    		right: 42%;
    	}
    }
    .check{
    	a{
			color: #008cd6;
		}
    }
    .code-box{
    	position: absolute;
    	top:0;
    	right: rem(180px);
    	border: solid 1px #f5f5f5;
    	background: #f5f5f5;
    	display: inline-block;
    	width: rem(225px);
    	height: rem(126px);
    	transition: all .3s ease-in-out;
    	img{
    		width: rem(225px);
    		height: rem(126px);
    	}
    }
    .change-code{
    	position: absolute;
    	top: 0;
    	right: rem(36px);
    	display: inline-block;
    	height: rem(135px);
    	width: rem(108px);
    	cursor: pointer;
    }
    input{
    	border: none;
	    width: 100%;
	    font-size: rem(60px);
	    line-height: rem(90px);
	    font-size: rem(50px);
	    color: #000;
    }
    .check-agree{
		width: rem(39px);
		height: rem(39px);
		float: left;
		margin-top:rem(45px);
		margin-left: rem(30px);
		font-size: rem(36px);
	}
    .name,.phone-yzm-inp{
    	width: 60%;
    }
    input:focus{
    	border: none;

    }
    #btn-phone-yzm{
    	width: 35%;
	    color: #fff;
	    background: #008cd6;
	    border-radius: rem(9px);
	    line-height: rem(90px);
	    font-size: rem(42px);
    }
}
button{
	width: 100%;
	border-radius: rem(9px);
    border: none;
    cursor: pointer;
    height: rem(144px);
    line-height: rem(144px);
    text-align: center;
    margin: 0 auto;
    font-size: rem(72px);
}
.login-btn{
	width: 95%;
	overflow: hidden;
	margin: 0px auto;
	button{
		background-color: #cce8f7;
		color: #008cd6;
	}
}
/*input 部分*/

/*other-login start*/
.other-login{
	text-align: center;
    width: 95%;
    margin: rem(36px) auto;
    font-size: rem(50px);
    p{
    	color: #a3cbeb;
   		margin: rem(36px) auto;
    }
    a {
	    width: rem(150px);
	    height: rem(150px);
	    overflow: hidden;
	    display: inline-block;
	    margin: 0 rem(30px);
	    border-radius:rem(18px);
	    b{
	    	width: rem(150px);
	    	height: rem(150px);
	    	display: inline-block;
		    overflow: hidden;
		    background: url('../../static/images/m-login-om-ico.png') no-repeat;
		    background-size: rem(714px) rem(96px);
	    }
	}
	.qq{
		background: #5ca3e1;
		b{
			background-position: rem(30px) center;
		}
	}
	.weibo{
		background: #ff5858;
		b{
			background-position: rem(-276px) center;
		}
	}
	.renren{
		background: #6a9bec;
		b{
			background-position:rem(-597px) center;
		}
	}
}
/*提示错误框*/
.err-msg{
	display: none;
	background-color: #F78B83;
	border-color: #D95252;
    color: #912323;
    border-radius: rem(12px);
    font-size: rem(36px);
    height: rem(72px);
    box-shadow: rem(3px) rem(3px) rem(9px) rem(3px) rgba(0,0,0,0.15);
}
.chang{
	width: rem(351px);
	position:absolute;
}
.one{
	
	top: rem(470px);
	left: rem(140px);
}
.duan{
	width: rem(315px);
	position:absolute;
}
.two{
	
	top: rem(605px);
	right: rem(170px);
}
.three{
	top: rem(740px);
	left: rem(140px);
}
.four{
	right: rem(170px);
	top:rem(875px);
}
.five{
	right: rem(170px);
	top:rem(1020px);	
}
.err-cont{
	position: absolute;
	top: 0;
	left: 0;
	display: inline-block;
    text-align: left;
    overflow: hidden;
    height: rem(72px);
    text-align: center;
    line-height: rem(72px);
    padding: 0 rem(27px);
}
.sanjiao{
	position: absolute;
	right: rem(-93px);
	top: rem(12px);
	display: inline-block;
	width: 0;
	height: 0;
	border-width: rem(24px) rem(48px);
	border-style: solid;
	border-color: transparent transparent transparent #F78B83;
}
</style>


