<template>
	<div>
		<transition name="fade">
			<div class="container" v-show="isShow">
				<header class="header">
				<div class="header-cont">
					<a href="" class="logo">
						<img src="../../stat../../static/images/m-vivo-footer-logo.png" alt="">
					</a>
					<p>账号登录</p>
				</div>
			</header>
			<div class="warp">
				<form action="">
					<ul class="login-list">
						<li class="name">
							<input type="text" placeholder="邮箱/手机号" style="outline:none;">
							<div class="errmessage">
								账号不能为空
							</div>
							<b class="error"></b>
						</li>
						<li class="password">
							<input type="password" placeholder="密码" style="outline:none;">
							<div class="errmessage">
								账号不能为空
							</div>
							<b class="error"></b>
						</li>
					</ul>
					<div class="login-btn">
						<button>立即登录</button>
					</div>
				</form>
				<div class="link">
					<a href="javascript:void(0)" @click="hide">忘记密码</a>
				</div>
				<div class="reg-btn">
					<button @click = "showRegist">注册vivo账号</button>
				</div>
				<div class="other-login">
					<p>其他方式登录</p>
					<a href="" class="qq">
						<b></b>
					</a>
					<a href="" class="weibo">
						<b></b>
					</a>
					<a href="" class="renren">
						<b></b>
					</a>
				</div>
			</div>
			</div>
		</transition>
		<Regist ref="regist"></Regist>
	</div>
</template>
<script>
import Regist from '@/components/regist'
	export default{
		data () {
			return{
				isShow:false
			}
		},
		components:{
			Regist
		},
		methods:{
			show:function(){

				this.isShow = true
			},
			hide () {
				this.isShow = false
			},
			showRegist () {
				this.$refs.regist.showabc()
			}
		}
	}
</script>

<style scoped lang="scss">
	@function rem($px) {
    @return $px/108px * 1rem;
}
*{
	padding: 0;
	margin: 0 auto;
}
.fade-enter{
	transform:translateX(100%);
}
.fade-enter-to{
	transform:translateX(0);
	transition: all .7s;
}
.fade-leave{
	transform:translateX(0);
}
.fade-leave-active{
	transform:translateX(-100%);
	transition: all .7s;
}
.container{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	font-family: "Arial";
    font-size: 75%;
    color: #000;
    background-color: #008cd6;
    margin: 0;
    padding: 0;
    font-weight: normal;
    height: 100%;
    a {
	    color: #999999;
	    text-decoration: none;
	    outline: none;
	    cursor: pointer;
	}
}
.header{
	width: 100%;
	overflow: hidden;
}
.header-cont{
	width: 100%;
	margin-bottom: rem(36px);
	p{
		font-size: rem(60px);
	    color: #fff;
	    font-weight: normal;
	    margin: 0 auto rem(75px);
	    text-align: center;
	}
}
.logo{
	display: block;
	width: rem(390px);
	height: rem(105px);
	margin:rem(90px) auto rem(75px);
	img{
		width: rem(390px);
		height: rem(105px);
	}
}
.login-list{
	border-radius: rem(15px);
    background-color: #fff;
    width: 95%;
    margin: rem(36px) auto rem(108px);
    li{
    	border-bottom: 1px solid #A3CBEB;
	    text-align: center;
	    line-height: rem(135px);
	    position: relative;
	    padding: 0 rem(36px);
	    text-align: left;
	    b {
		    width: rem(69px);
		    height: rem(69px);
		    overflow: hidden;
		    background: url('../../static/images/m-regjudge-ico.png') no-repeat;
		    background-size:rem(138px) rem(69px);
		    position: absolute;
		    top: rem(30px);
		    right: rem(24px);
		    display: none;
		}
		.current{
			background-position: rem(-69px) 0;
			display: block;
		}
    }
    input{
    	border: none;
	    width: 100%;
	    font-size: rem(60px);
    }
    input:focus{
    	border: none;

    }
    .errmessage{
    	position: absolute;
	    top: 0px;
	    right: rem(105px);
	    display: none;
    }
}
button{
	width: 100%;
	border-radius: rem(9px);
    border: none;
    cursor: pointer;
    height: rem(144px);
    line-height: rem(144px);
    text-align: center;
    margin: 0 auto;
    font-size: rem(72px);
}
.login-btn{
	width: 95%;
	overflow: hidden;
	margin: 0px auto;
	button{
		background-color: #cce8f7;
		color: #008cd6;
	}
}
.reg-btn{
	width: 95%;
	overflow: hidden;
	margin: 0px auto;
	button{
		color: #008cd6;
		background-color: #FFF;
	}
}
/*link start*/
.link{
	text-align: right;
    width: 95%;
    margin: rem(72px) auto;
    a{
    	color: #a3cbeb;
	    margin: rem(72px);
	    font-size: rem(50px);
    }
}
/*other-login start*/
.other-login{
	text-align: center;
    width: 95%;
    margin: rem(36px) auto;
    font-size: rem(50px);
    p{
    	color: #a3cbeb;
   		margin: rem(36px) auto;
    }
    a {
	    width: rem(150px);
	    height: rem(150px);
	    overflow: hidden;
	    display: inline-block;
	    margin: 0 rem(30px);
	    border-radius:rem(18px);
	    b{
	    	width: rem(150px);
	    	height: rem(150px);
	    	display: inline-block;
		    overflow: hidden;
		    background: url('../../static/images/m-login-om-ico.png') no-repeat;
		    background-size: rem(714px) rem(96px);
	    }
	}
	.qq{
		background: #5ca3e1;
		b{
			background-position: rem(30px) center;
		}
	}
	.weibo{
		background: #ff5858;
		b{
			background-position: rem(-276px) center;
		}
	}
	.renren{
		background: #6a9bec;
		b{
			background-position:rem(-597px) center;
		}
	}
}
	
</style>


