import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import List from '@/components/List'
import Shop from '@/components/Shop'
import Person from '@/components/Person'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/list',
      name: 'List',
      component: List
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/person',
      name: 'Person',
      component: Person
    }
  ]
})



